import server from '../../server.js';
export default {
	data() {
		return {
			topList:[],
			userinfo:[]
		}
	},
	onLoad:function(){
		this.getTop();
		this.userinfo=server.userinfo;
		
	},
	methods: {
		getBire:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getTop:function(){
			uni.showLoading({
				title: '获取数据中'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=weekTop", //仅为示例，并非真实接口地址。
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie
				},
				success: (res) => {
					if(res.data.code=="1"){
						this.topList=res.data.data
					}else{
						uni.showToast({
							title: "获取失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					uni.hideLoading();
				}
			});
		}
		
	}
}
